import { IS_CREATED, IS_DONE, IS_PENDING } from './type';

export const getStatus = (statusId: string) => {
    let status = '';
    switch (statusId) {
        case IS_DONE:
            status = '';
            break;
        case IS_PENDING:
            status = '';
            break;
        case IS_CREATED:
            status = '';
            break;
    }
    return status;
}

export const cleanToken = (token: string | null | undefined) => {
    return token && token.replace(/^Bearer /, '');
}